import React, { useState, useEffect, useRef } from 'react';
import logoSvg from '../assets/images/logo/logo.svg';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

import SearchSection from './SearchSection';
gsap.registerPlugin(ScrollTrigger);
const Nav = ({ tags, suggs }) => {
  const [isSearchOpen, setIsSearchOpen] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const searchTimeline = useRef(gsap.timeline({ paused: true }));
  const menuTimeline = useRef(gsap.timeline({ paused: true }));

  useEffect(() => {
    // Search timeline setup
    searchTimeline.current.to(".searchFrame", {
      y: "0", rotateZ: '0deg', skewY: "0deg", transformOrigin: '0% 100%', scale: 1, duration: 0.7, ease: "power3.inOut",
    }).to("main", {
      y: "80vh", rotateZ: '4deg', opacity: 0, transformOrigin: '0% 0%', scale: 1.8, duration: 0.7, ease: "power3.inOut",
    }, 0).to(".suglinks", {
      y: "0%", stagger: 0.1, rotate: 0, transformOrigin: '0% 100%', duration: 0.4
    }).to(".search-btn path", {
      fill: "#CF4116", duration: 0.8
    }, 0);

    // Menu timeline setup
    menuTimeline.current.to(".menuFrame", {
      y: "0", rotateZ: '0deg', skewY: "0deg", transformOrigin: '0% 100%', scale: 1, duration: 0.7, ease: "power3.inOut",
    }).to("main", {
      y: "80vh", rotateZ: '4deg', opacity: 0, transformOrigin: '0% 0%', scale: 1.8, duration: 0.7, ease: "power3.inOut",
    }, 0).to(".navlinks", {
      y: "0%", stagger: 0.1, rotate: 0, transformOrigin: '0% 100%', duration: 0.4
    }).to(".menu-svgs", {
      stroke: "#CF4116", duration: 0.8, stagger: 0.3
    }, 0);

  }, []);

  const toggleSearch = () => {
    if (isMenuOpen) {
      menuTimeline.current.reverse();
      document.body.classList.remove("overflowhidden");
      setIsMenuOpen(false);
    }
    if (!isSearchOpen) {
      searchTimeline.current.play();
      document.body.classList.add("overflowhidden");
    } else {
      searchTimeline.current.reverse();
      document.body.classList.remove("overflowhidden");
    }
    setIsSearchOpen(!isSearchOpen);
  };

  const toggleMenu = () => {
    if (isSearchOpen) {
      searchTimeline.current.reverse();
      document.body.classList.remove("overflowhidden");
      setIsSearchOpen(false);
    }
    if (!isMenuOpen) {
      menuTimeline.current.play();
      document.body.classList.add("overflowhidden");
    } else {
      menuTimeline.current.reverse();
      document.body.classList.remove("overflowhidden");
    }
    setIsMenuOpen(!isMenuOpen);
  };

  // Logic for showing/hiding menu button on scroll (from your original script)
  useEffect(() => {
    if (window.innerWidth > 640) {
      ScrollTrigger.create({
        trigger: "main",
        start: () => `${document.querySelector(".nav-bar")?.offsetHeight} ${document.querySelector(".nav-bar")?.offsetHeight / 2}`,
        end: () => `bottom ${document.querySelector(".nav-bar")?.offsetHeight / 2}`,
        onEnter: () => {
          // This will show the menu button for desktop
          document.querySelector(".menu-btn")?.classList.remove("hidden");
          document.querySelector(".sm\\:hidden")?.classList.remove("hidden"); // Ensure the flex sm:hidden class is removed
          document.querySelector(".nav-bar-links")?.classList.add("hidden");
          document.querySelectorAll(".menu-svgs").forEach(el => el.classList.remove("onEntry-effect-right"));
          document.querySelectorAll(".menu-svgs").forEach(el => el.removeAttribute("style"));
        },
        onLeaveBack: () => {
          // This will hide the menu button for desktop
          document.querySelector(".nav-bar-links")?.classList.remove("hidden");
          document.querySelector(".sm\\:hidden")?.classList.add("hidden");
          document.querySelector(".menu-btn")?.classList.add("hidden");
        },
      });
    }
  }, []);


  return (
    <>
      <nav className="nav-bar">
        <div className="nav-bar-logo overflow-hidden">
          <a className="onEntry-effect" href="/">
            <img src={logoSvg} alt="trikuta-travel-expert-logo" />
          </a>
        </div>
        <div className="flex justify-center items-center overflow-hidden">
          <button className="overflow-hidden search-btn" onClick={toggleSearch}>
            <svg className="onEntry-effect-right" width="17" height="18" viewBox="0 0 30 31" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M23.6748 22.0866L30 28.4103L27.9103 30.5L21.5866 24.1748C19.2336 26.061 16.307 27.0869 13.2913 27.0827C5.95451 27.0827 0 21.1281 0 13.7913C0 6.45451 5.95451 0.5 13.2913 0.5C20.6281 0.5 26.5827 6.45451 26.5827 13.7913C26.5869 16.807 25.561 19.7336 23.6748 22.0866ZM20.7123 20.9908C22.5866 19.0634 23.6333 16.4798 23.629 13.7913C23.629 8.07901 19.0022 3.45363 13.2913 3.45363C7.57901 3.45363 2.95363 8.07901 2.95363 13.7913C2.95363 19.5022 7.57901 24.129 13.2913 24.129C15.9798 24.1333 18.5634 23.0866 20.4908 21.2123L20.7123 20.9908Z" className="fill-prime-dark" />
            </svg>
          </button>
          <button className="flex sm:hidden menu-btn pl-7 p-3 text-prime-dark" onClick={toggleMenu}>
            <svg width="31" height="13" viewBox="0 0 36 18" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path className="menu-svgs onEntry-effect-right" d="M13 1L35 0.999998" stroke="#222222" strokeWidth="2" strokeLinecap="round" />
              <path className="menu-svgs onEntry-effect-right" d="M8 9L35 9" stroke="#222222" strokeWidth="2" strokeLinecap="round" />
              <path className="menu-svgs onEntry-effect-right" d="M1 17H35" stroke="#222222" strokeWidth="2" strokeLinecap="round" />
            </svg>
          </button>

          <div className="nav-bar-links hidden sm:flex">
            <ul className="flex flex-row p-4">
              <li className="overflow-hidden">
                <p className="onEntry-effect"><a href="/">Home</a></p>
              </li>
              <li className="overflow-hidden">
                <p className="onEntry-effect"><a href="/about">About</a></p>
              </li>
              {/* <li className="overflow-hidden">
                <p className="onEntry-effect"><a href="/holidays">Holidays</a></p>
              </li>
              <li className="overflow-hidden">
                <p className="onEntry-effect"><a href="/gallery">Gallery</a></p>
              </li> */}
            </ul>
          </div>
        </div>
      </nav>

      {/* Menu Frame */}
      <div className="menuFrame">
        <ul>
          <li className="overflow-hidden">
            <p className="navlinks"><a href="/">Home</a></p>
          </li>
          <li className="overflow-hidden">
            <p className="navlinks"><a href="/about">About</a></p>
          </li>
          <li className="overflow-hidden">
            <p className="navlinks"><a href="/holidays">Holidays</a></p>
          </li>
          <li className="overflow-hidden">
            <p className="navlinks"><a href="/gallery">Gallery</a></p>
          </li>
        </ul>
      </div>

      {/* Search Section */}
      <SearchSection tags={tags} suggs={suggs} />
    </>
  );
};

export default Nav;