import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import TitleWrapper from './TitleWrapper'; // Assuming TitleWrapper is correctly imported

// We'll still directly use require.context for themes since they are at the root
// of the themes folder, not in subfolders.
// If your images ARE in subfolders (e.g., assets/images/themes/spiritual/spiritual.webp),
// then let me know and we can adjust to use loadImagesFromCategoryFolder more directly.
const themesContext = require.context('../assets/images/themes/', false, /\.(png|webp|jpe?g|svg|gif)$/);

const ThemesPage = () => {
  const themesData = [
    { name: 'Spiritual', url: 'spiritual', image: 'spiritual' },
    { name: 'Adventure', url: 'adventure', image: 'adventure' },
    { name: 'Honeymoon', url: 'haneymoon', image: 'honeymoon' },
    { name: 'Cultural', url: 'cultural', image: 'cultural' },
  ];

  const [themeImages, setThemeImages] = useState({});

  useEffect(() => {
    const loadedImages = {};
    themesData.forEach(theme => {
      const imageKey = `./${theme.image}.webp`; // Assuming .webp extension
      if (themesContext.keys().includes(imageKey)) {
        loadedImages[theme.image] = themesContext(imageKey);
      } else {
        console.warn(`Image not found for theme: ${theme.name} at path: ${imageKey}`);
      }
    });
    setThemeImages(loadedImages);
  }, []); // Empty dependency array means this runs once on mount

  const getThemeImageUrl = (imageName) => {
    return themeImages[imageName] || '';
  };

  return (
    <>
      {/* makeTitle2("themes") is replaced by TitleWrapper */}
      <TitleWrapper title="themes" />

      <div className="grid super-padding grid-cols-1 sm:grid-cols-2 super-gap">
        {/* First column with sm:mt-52 */}
        <div className="grid super-gap sm:mt-52">
          {/* Spiritual Theme */}
          <Link to={`/holidays/${themesData[0].url}`}>
            <div className="overflow-hidden aspect-square">
              <img
                className="image-random-move"
                src={getThemeImageUrl(themesData[0].image)}
                alt={`${themesData[0].name}-theme-trikuta-travel-expert`}
              />
            </div>
            <div className="overflow-hidden">
              <div
                className="py-3 flex items-center text-xs uppercase before:flex-[1_1_0%] before:border-t before:border-primary-500 before:me-4 after:flex-[1_1_0%] after:border-t after:border-primary-500 after:ms-4 entry-element">
                <h4 className="normal-bold">{themesData[0].name}</h4>
              </div>
            </div>
          </Link>

          {/* Adventure Theme */}
          <Link to={`/holidays/${themesData[1].url}`}>
            <div className="overflow-hidden aspect-square">
              <img
                className="image-random-move"
                src={getThemeImageUrl(themesData[1].image)}
                alt={`${themesData[1].name}-theme-trikuta-travel-expert`}
              />
            </div>
            <div className="overflow-hidden">
              <div
                className="py-3 flex items-center text-xs uppercase before:flex-[1_1_0%] before:border-t before:border-primary-500 before:me-4 after:flex-[1_1_0%] after:border-t after:border-primary-500 after:ms-4 entry-element">
                <h4 className="normal-bold">{themesData[1].name}</h4>
              </div>
            </div>
          </Link>
        </div>

        {/* Second column with sm:gap-0 */}
        <div className="grid super-gap sm:gap-0">
          {/* Honeymoon Theme */}
          <Link to={`/holidays/${themesData[2].url}`}>
            <div className="overflow-hidden aspect-square">
              <img
                className="image-random-move"
                src={getThemeImageUrl(themesData[2].image)}
                alt={`${themesData[2].name}-theme-trikuta-travel-expert`}
              />
            </div>
            <div className="overflow-hidden">
              <div
                className="py-3 flex items-center text-xs uppercase before:flex-[1_1_0%] before:border-t before:border-primary-500 before:me-4 after:flex-[1_1_0%] after:border-t after:border-primary-500 after:ms-4 entry-element">
                <h4 className="normal-bold">{themesData[2].name}</h4>
              </div>
            </div>
          </Link>

          {/* Cultural Theme */}
          <Link to={`/holidays/${themesData[3].url}`}>
            <div className="overflow-hidden aspect-square">
              <img
                className="image-random-move"
                src={getThemeImageUrl(themesData[3].image)}
                alt={`${themesData[3].name}-theme-trikuta-travel-expert`}
              />
            </div>
            <div className="overflow-hidden">
              <div
                className="py-3 flex items-center text-xs uppercase before:flex-[1_1_0%] before:border-t before:border-primary-500 before:me-4 after:flex-[1_1_0%] after:border-t after:border-primary-500 after:ms-4 entry-element">
                <h4 className="normal-bold">{themesData[3].name}</h4>
              </div>
            </div>
          </Link>
        </div>
      </div>
    </>
  );
};

export default ThemesPage;