import AppRouter from "./routes/routes";
import ParticlesScene from "./components/particlesScene";
import Header from "./components/layouts/header";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import TitleWrapper from "./components/TitleWrapper"; // Adjust the path if necessary
import Lenis from "@studio-freight/lenis";
import { useState, useEffect, React } from "react";
import SiteContext from "./context/siteContext";
import Preloader from "./components/Preloader";
import Nav from "./components/Nav";
import FixedPageButtons from "./components/FixedPageButtons";
import HomePage from "./pages/homePage";
import Footer from "./components/Footer";
import { HashRouter } from "react-router-dom";
gsap.registerPlugin(ScrollTrigger);

function App() {
  const getTheme = () =>
    window.matchMedia("(prefers-color-scheme: dark)").matches ? true : false;
  const [theme, setTheme] = useState(getTheme);
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  useEffect(() => {
    const lenis = new Lenis({
      duration: 2,
      easing: (t) => Math.min(1, 1.001 - Math.pow(2, -10 * t)),
      direction: "vertical",
      smooth: true,
    });

    function raf(time) {
      lenis.raf(time);
      requestAnimationFrame(raf);
    }

    requestAnimationFrame(raf);

    return () => lenis.destroy();
  }, []);
  useEffect(() => {
    // GSAP Loader Animation
    const loaderTimeline = gsap.timeline({
      paused: true,
      onComplete: () => {
        document.body.classList.remove("overflowhidden");
        document.querySelector(".preloader")?.remove();
      },
    });

    loaderTimeline
      .to(".preloader", {
        x: "-100vw",
        duration: 0.2,
      })
      .to(
        ".onEntry-effect",
        {
          y: 0,
          rotateZ: 0,
          stagger: 0.1,
          duration: 0.5,
        },
        "<0.4"
      )
      .to(
        ".onEntry-effect-right",
        {
          x: 0,
          stagger: 0.1,
          duration: 0.5,
        },
        "<0.4"
      );

    loaderTimeline.play();

    // Initialize Lenis for smooth scrolling
    const lenis = new Lenis();
    lenis.on("scroll", ScrollTrigger.update);
    gsap.ticker.add((time) => {
      lenis.raf(time * 1000);
    });

    // GSAP Common Animations (moved from bodyEnd.php script)
    gsap.utils.toArray(".center-heighlight").forEach((element) => {
      gsap.to(element, {
        scrollTrigger: {
          trigger: element,
          start: "top 50%",
          end: () => `${element.parentElement.offsetHeight} 40%`,
          toggleActions: "play reset play reset",
        },
        color: "#CF4116",
      });
    });
    gsap.utils.toArray(".center-lyt").forEach((element) => {
      gsap.to(element, {
        scrollTrigger: {
          trigger: element,
          start: "top center",
          end: "bottom center",
          toggleActions: "play reset play reset",
        },
        color: "#CF4116",
        duration: 0.00001,
      });
    });
    gsap.utils.toArray(".paralax-scroll").forEach((element) => {
      gsap.to(element, {
        scrollTrigger: {
          trigger: element,
          start: "top 90%",
          end: "bottom top",
          scrub: 1,
        },
        yPercent: gsap.utils.random(-35, -15),
      });
    });
    gsap.utils.toArray(".entry-element-right-scrub").forEach((element) => {
      gsap.to(element, {
        scrollTrigger: {
          trigger: element,
          start: "top 90%",
          end: "bottom 60%",
          scrub: 1.3,
        },
        x: "0%",
      });
    });
    gsap.utils.toArray(".entry-element-left-scrub").forEach((element) => {
      gsap.to(element, {
        scrollTrigger: {
          trigger: element,
          start: "top 90%",
          end: "bottom 60%",
          scrub: 1.3,
        },
        x: "0%",
      });
    });
    gsap.utils.toArray(".image-random-move").forEach((img_element) => {
      gsap.to(img_element, {
        scrollTrigger: {
          trigger: img_element,
          start: "top bottom",
          scrub: 1.3,
        },
        y: "-9.5%",
      });
    });
    gsap.utils.toArray(".entry-fade").forEach((element) => {
      gsap.to(element, {
        scrollTrigger: { trigger: element, start: "top 80%" },
        opacity: 1,
        duration: 1,
      });
    });
    gsap.utils.toArray(".entry-element").forEach((element) => {
      gsap.to(element, {
        scrollTrigger: { trigger: element, start: "-100% 84%" },
        y: "0%",
        rotate: 0,
        stagger: 3,
        duration: 1,
      });
    });

    gsap.utils.toArray(".entry-element-rev").forEach((element) => {
      gsap.to(element, {
        scrollTrigger: { trigger: element, start: "-100% 84%" },
        y: "0%",
        stagger: 0.3,
        rotate: 0,
        duration: 1,
      });
    });

    gsap.utils.toArray(".entry-img").forEach((element) => {
      gsap.to(element, {
        scrollTrigger: {
          trigger: element,
          start: "center 90%",
          end: "bottom top",
          scrub: 2,
        },
        y: "-8%",
        stagger: 0.3,
      });
    });
    const directionAnimation = gsap.to(".direction-svg", {
      rotateZ: 200,
    });

    ScrollTrigger.create({
      trigger: ".direction-section",
      start: "top 80%",
      end: "bottom 40%",
      animation: directionAnimation,
      scrub: 1,
    });
    // Helix effect (if you have an element with class "helix")
    let proxy = { y: 0 };
    let ySetter = gsap.quickSetter(".helix", "y", "px");
    let clamp = gsap.utils.clamp(-60, 60);
    ScrollTrigger.create({
      onUpdate: (self) => {
        let y = clamp(self.getVelocity() / -300);
        if (Math.abs(y) > Math.abs(proxy.y)) {
          proxy.y = y;
          gsap.to(proxy, {
            y: 0,
            duration: 0.8,
            ease: "power3",
            overwrite: true,
            onUpdate: () => ySetter(proxy.y),
          });
        }
      },
    });
  }, []); // Empty dependency array means this runs once on mount

  return (
    <>
      <SiteContext.Provider
        value={{
          theme,
          setTheme,
          isMenuOpen,
          setIsMenuOpen,
        }}
      >
        <Preloader />
        {/* Modal for query submission */}
        <div className="fixed top-2/4 left-1/2 z-40 hidden -translate-x-1/2 rounded-lg shadow bg-prime-white w-3/5 sm:w-1/2 place-self-center modalx">
          <div className="p-6 md:p-7 text-center">
            <h3 className="mb-5 text-lg font-normal queryReturn">
              Query Successfully Submitted. We will call you Back
            </h3>
            <button className="button-neutral modalxclose">Close</button>
          </div>
        </div>
        <Nav />
        <FixedPageButtons /> {/* Render the fixed page buttons here */}
        <main className="main">
          
          {/* <HomePage /> */}
          <HashRouter>

        <AppRouter />
          </HashRouter>
        </main>
        <Footer />
        {/* <Header /> */}
        {/* <ParticlesScene /> */}
      </SiteContext.Provider>
    </>
  );
}
export default App;
