import React, { useEffect, useRef } from 'react';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import TitleWrapper from './TitleWrapper';

gsap.registerPlugin(ScrollTrigger);

const StorySection = ({ reviews }) => {
  // Use refs for elements that need GSAP animation targeting
  const titleRef = useRef(null);
  const reviewRefs = useRef([]); // To hold refs for each review paragraph

  useEffect(() => {
    // Animate the title
    if (titleRef.current) {
      gsap.fromTo(titleRef.current,
        { opacity: 0, y: 50 }, // Initial state (invisible, moved down)
        {
          opacity: 1,
          y: 0,
          duration: 1,
          ease: "power2.out",
          scrollTrigger: {
            trigger: titleRef.current,
            start: "top 80%", // When top of title enters 80% of viewport
            toggleActions: "play none none none", // Play on enter, do nothing else
          }
        }
      );
    }

    // Apply 'entry-fade' to paragraphs and 'entry-element' to review names
    reviewRefs.current.forEach(el => {
      if (el) {
        gsap.to(el.querySelector('.entry-fade'), {
          scrollTrigger: {
            trigger: el.querySelector('.entry-fade'),
            start: "top 80%"
          },
          opacity: 1,
          duration: 1,
        });

        gsap.to(el.querySelector('.entry-element'), {
          scrollTrigger: {
            trigger: el.querySelector('.entry-element'),
            start: "-100% 84%"
          },
          y: "0%",
          rotate: 0,
          stagger: 3,
          duration: 1,
        });
      }
    });

  }, []); // Empty dependency array means this effect runs once after the initial render

  return (
    <section id="section2">
      <div className="story-section">
            <TitleWrapper title="Stories" />

        <div className="grid super-padding grid-cols-1 sm:grid-cols-2 super-gap">

          <div className="grid super-gap sm:mt-52">
            {reviews.slice(0, 2).map((review, index) => (
              <div key={index} ref={el => reviewRefs.current[index] = el}>
                <p className="entry-fade"><span className="font-PlayfairDisplay font-bold">&ldquo;</span>{review.reviewPara}<span className="font-PlayfairDisplay font-bold">&rdquo;</span> </p>
                <div className="overflow-hidden">
                  <div className="py-3 flex items-center text-xs uppercase before:flex-[1_1_0%] before:border-t before:border-primary-500 before:me-4 entry-element">
                    <h4 className="normal-bold">{review.reviewerName}</h4>
                  </div>
                </div>
              </div>
            ))}
          </div>
          <div className="grid super-gap sm:gap-0">
            {reviews.slice(2, 4).map((review, index) => (
              <div key={index + 2} ref={el => reviewRefs.current[index + 2] = el}> {/* Use index + 2 for unique key */}
                <p className="entry-fade"><span className="font-PlayfairDisplay font-bold">&ldquo;</span>{review.reviewPara}<span className="font-PlayfairDisplay font-bold">&rdquo;</span> </p>
                <div className="overflow-hidden">
                  <div className="py-3 flex items-center text-xs uppercase before:flex-[1_1_0%] before:border-t before:border-primary-500 before:me-4 entry-element">
                    <h4 className="normal-bold">{review.reviewerName}</h4>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
      {/* Services section follows in the original, so we'll put it here or as a separate component */}
    </section>
  );
};

export default StorySection;