// src/utils/imageLoader.js

// Define contexts for known root image directories
const holidayContext = require.context('../assets/images/holidays/', true, /\.(png|jpe?g|svg|gif)$/);
const galleryContext = require.context('../assets/images/gallery/', true, /\.(png|jpe?g|svg|gif)$/);
const themesContext = require.context('../assets/images/themes/', true, /\.(png|webp|jpe?g|svg|gif)$/);
const usefullContext = require.context('../assets/images/usefull/', false, /\.(png|webp|jpe?g|svg|gif)$/);
// NEW: Context for logo images, assuming they are directly in the 'logo' folder
const logoContext = require.context('../assets/images/logo/', false, /\.(png|webp|jpe?g|svg|gif)$/);

// A map to quickly get the right context based on category string
const contexts = {
  'holidays': holidayContext,
  'gallery': galleryContext,
  'themes': themesContext,
  'usefull': usefullContext,
  'logo': logoContext // NEW: Added new mapping for logos
};

/**
 * Dynamically imports all images from a specific subfolder within a given category, or a specific file from a root category.
 * @param {string} category - The top-level image category (e.g., 'holidays', 'gallery', 'usefull', 'logo').
 * @param {string} fileNameOrSubfolderName - The name of the specific file (e.g., 'group.webp', 'orcapixel.png') or subfolder (e.g., 'Holiday20240205072309').
 * @returns {object|string} An object where keys are image filenames and values are URLs (for subfolders), or a direct URL for a single file.
 */
const loadImagesFromCategory = (category, fileNameOrSubfolderName=null) => {
  const context = contexts[category];
  if(!fileNameOrSubfolderName){
    const images = {};
    try {
      context.keys().forEach((key) => {
    
        const relativePath = key.replace('./', ''); // Remove leading './'
        const fileNameWithoutExtension = relativePath.split('.')[0]; // Get the filename without extension
        images[fileNameWithoutExtension] = context(key); // Store the image URL with filename as key
      
      });
      console.log('====================================');
      console.log(images);
      console.log('====================================');
    } catch (error) {
      console.error(`Error loading images for category "${category}" subfolder "${fileNameOrSubfolderName}":`, error);
      return {};
    }
    return images; 
  }
  if (!context) {
    console.warn(`No image context defined for category: "${category}". Check imageLoader.js.`);
    return ['usefull', 'logo'].includes(category) ? '' : {}; // Return empty string for single file contexts, empty object for subfolders
  }

  // Check if the context is for direct files (non-recursive contexts)
  if (category === 'usefull' || category === 'logo') {
    const imagePath = `./${fileNameOrSubfolderName}`;
    if (context.keys().includes(imagePath)) {
      return context(imagePath); // Return the direct URL for the single file
    } else {
      console.warn(`Image "${fileNameOrSubfolderName}" not found in category "${category}".`);
      return '';
    }
  } else {
    // Original logic for subfolders (e.g., holidays, gallery, themes if they use subfolders)
    const images = {};
    try {
      context.keys().forEach((key) => {
        if (key.startsWith(`./${fileNameOrSubfolderName}/`)) {
          const relativePath = key.replace(`./${fileNameOrSubfolderName}/`, '');
          const fileNameWithoutExtension = relativePath.split('.')[0];
          images[fileNameWithoutExtension] = context(key);
        }
      });
    } catch (error) {
      console.error(`Error loading images for category "${category}" subfolder "${fileNameOrSubfolderName}":`, error);
      return {};
    }
    return images;
  }
};

export default loadImagesFromCategory;