const FixedPageButtons = () => {
  const buttonRefs = useRef([]);

  const scrollToSection = (index) => {
    const section = document.getElementById(`section${index}`);
    if (section) {
      const navBarHeight = document.querySelector(".nav-bar")?.offsetHeight || 0;
      window.scrollTo({
        top: section.offsetTop - navBarHeight,
        behavior: "smooth"
      });
    }
  };

  useEffect(() => {
    // GSAP ScrollTrigger for active button state
    buttonRefs.current.forEach((btn, index) => {
      gsap.to(btn, {
        scrollTrigger: {
          trigger: `#section${index}`,
          start: "top 60%",
          end: "bottom 60%",
          onEnter: () => {
            buttonRefs.current.forEach(b => b.querySelector("path")?.classList.remove("active-posi"));
            btn.querySelector("path")?.classList.add("active-posi");
          },
          onEnterBack: () => {
            buttonRefs.current.forEach(b => b.querySelector("path")?.classList.remove("active-posi"));
            btn.querySelector("path")?.classList.add("active-posi");
          },
          onLeave: () => {
            btn.querySelector("path")?.classList.remove("active-posi");
          },
          onLeaveBack: () => {
            btn.querySelector("path")?.classList.remove("active-posi");
          },
        }
      });
    });
  }, []);

  return (
    <div className="page-posi ">
      <div className="flex justify-evenly flex-col items-center onEntry-effect-right">
        <button id="btn0" onClick={() => scrollToSection(0)} ref={el => buttonRefs.current[0] = el}>
          <HomeIcon className="p-2" />
        </button>

        <svg width="1" height="30" viewBox="0 0 1 41" fill="none" xmlns="http://www.w3.org/2000/svg" className="">
          <line x1="0.5" y1="0.357422" x2="0.499998" y2="40.3574" stroke="#222222" />
        </svg>

        <button id="btn1" onClick={() => scrollToSection(1)} ref={el => buttonRefs.current[1] = el}>
          <HeartIcon className="p-2" />
        </button>

        <svg width="1" height="30" viewBox="0 0 1 41" fill="none" xmlns="http://www.w3.org/2000/svg" className="">
          <line x1="0.5" y1="0.357422" x2="0.499998" y2="40.3574" stroke="#222222" />
        </svg>

        <button id="btn2" onClick={() => scrollToSection(2)} ref={el => buttonRefs.current[2] = el}>
          <UserIcon className="p-2" />
        </button>

        <svg width="1" height="30" viewBox="0 0 1 41" fill="none" xmlns="http://www.w3.org/2000/svg" className="">
          <line x1="0.5" y1="0.357422" x2="0.499998" y2="40.3574" stroke="#222222" />
        </svg>

        <a className="cursorhovereffect text-regular" href="tel:+918317241234">
          <PhoneIcon className="p-2 fill-prime-dark" />
        </a>

        <svg width="1" height="30" viewBox="0 0 1 41" fill="none" xmlns="http://www.w3.org/2000/svg" className="">
          <line x1="0.5" y1="0.357422" x2="0.499998" y2="40.3574" stroke="#222222" />
        </svg>

        <a href="https://wa.me/+918317241234?text=Hi">
          <WhatsappIcon className="p-2 fill-prime-dark" />
        </a>
      </div>
    </div>
  );
};

export default FixedPageButtons;